
/* xx-styled is used in the frontend and backend, it is isolated here to prevent duplication */
@mixin xxstyled-innards{
  	box-sizing: border-box;
  	position:relative;
	margin:0px auto;

	// Foreground, Background, Padding
	font-family:var(--foregroundCopyFont);
	color: var(--foregroundColor);
	background-image: var(--backgroundImage);
	background-color: var(--backgroundColor);
	background-position: var(--backgroundPosition);
	background-attachment: var(--backgroundAttachment);
	background-repeat: var(--backgroundRepeat);
	background-size: var(--backgroundSizeSm);
  	padding:.01px;
	padding: var(--spacingMobile);

  // Modifier: Block
  &.xx-styled--block{
    max-width:var(--contentWidth);
  }

  // Modifier: HTML
  &.xx-styled--html{
    min-height:100%;
  }

   // Medium Screens
	@media(min-width: 992px){ 
		background-size: var(--backgroundSizeMd);
		padding: var(--spacingTablet); 
	}

  // Large Screens 
	@media(min-width:1200px){ 
		background-size: var(--backgroundSize);
		padding: var(--spacingDesktop); 
	}

  // Box Sizing
  *{  box-sizing: border-box; }

  // Modifier: Align Full
	&.alignfull{ max-width:none; }

  // Modifier: Align Wide
	&.alignwide{ max-width:var(--wideWidth); }

	// Selection
	*::selection {
		color: var(--selectionFGColor);
		background-color: var(--selectionBGColor);
	}

}